/*
ðŸŽ¯ MAIN ADJUSTMENT POINTS:

Colors:
- $c-primary, $c-secondary â†’ All gradients auto-adjust
- $c-body, $c-black, $c-white â†’ Used everywhere

Button System (only change these 4):
- $b-padding-base: 14px 28px
- $b-font-size-base: 16px  
- $b-borderRadius-base: $borderRadiusS (16px)
- $b-borderWidth-base: 1.5px

Spacing:
- $spaceBase: 100px â†’ All spacing scales from this

Border Radius:
- Use existing $borderRadiusXS to $borderRadiusL system

Everything else auto-calculates!
*/
:root {
  --color-primary: #054b1f;
  --color-primary-hover: #076d2a;
  --color-primary-light: #2ec8f4;
  --color-secondary: red;
  --color-text: #363636;
  --color-heading: #222222;
  --color-body: #363636;
  --color-white: #fff;
  --color-black: #222222;
  --color-gray: #6a747c;
  --color-gray-light: #f2f2f2;
  --color-gray-bg: #f8f8f8;
  --color-success: #10b981;
  --color-warning: #f59e0b;
  --color-accent: #8b5cf6;
}

.buttonRegular {
  color: #fff;
  background: #054b1f;
  border-color: #054b1f;
}

.buttonInverted {
  color: #054b1f;
  background: #fff;
  border-color: #054b1f;
}

.wp-core-ui.login {
  background: #fff;
}
.wp-core-ui.login #login {
  position: relative;
}
.wp-core-ui.login #login form {
  background: transparent;
  border: 3px solid #054b1f;
}
.wp-core-ui.login #login form .input,
.wp-core-ui.login #login form input[type=text] {
  background-color: transparent !important;
}
.wp-core-ui.login #login label {
  color: #363636;
  font-size: 12px;
}
.wp-core-ui.login #login a {
  color: #054b1f !important;
  text-decoration: none;
  transition: 0.3s ease all;
}
.wp-core-ui.login #login a:hover {
  opacity: 0.7;
}
.wp-core-ui.login #login h1 a {
  background-image: url(https://example.de/wp-content/themes/dbw-base/assets/img/logo.svg);
  width: 80%;
  max-height: 120px;
  background-size: contain;
  background-repeat: no-repeat;
  margin-inline: auto;
  height: auto;
  min-height: 50px;
}
.wp-core-ui.login #login .forgetmenot {
  display: inline-flex;
  align-items: center;
}
.wp-core-ui.login #login .forgetmenot > * {
  margin-block: 0;
  line-height: 1;
  transition: 0.3s ease all;
}
.wp-core-ui.login #login .forgetmenot:hover input[type=checkbox] {
  border-color: #054b1f !important;
}
.wp-core-ui.login #login .forgetmenot input[type=checkbox] {
  width: 20px;
  height: 20px;
  position: relative;
  margin-right: 10px;
}
.wp-core-ui.login #login .forgetmenot input[type=checkbox]:checked:before {
  content: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2024%2024%22%20width%3D%2220%22%20height%3D%2220%22%20role%3D%22presentation%22%20class%3D%22components-checkbox-control__checked%22%20aria-hidden%3D%22true%22%20focusable%3D%22false%22%3E%3Cpath%20d%3D%22M16.7%207.1l-6.3%208.5-3.3-2.5-.9%201.2%204.5%203.4L17.9%208z%22%20fill%3D%22%23fff%22%2F%3E%3C%2Fsvg%3E");
  width: 20px;
  height: 20px;
  margin: 0;
  position: absolute;
  top: -1px;
  left: -1px;
}
.wp-core-ui.login #login #nav,
.wp-core-ui.login #login #backtoblog {
  text-align: center;
}
.wp-core-ui.login #login #backtoblog a {
  color: #363636 !important;
}
.wp-core-ui.login #login .privacy-policy-page-link {
  margin-block: 25px;
}
.wp-core-ui.login #login .privacy-policy-page-link a {
  font-size: 14px;
}
.wp-core-ui.login input {
  border-radius: unset;
  border-color: #054b1f !important;
  color: #363636;
  background: transparent;
  font-size: 14px;
  transition: 0.3s ease all;
}
.wp-core-ui.login input:focus {
  border-color: #054b1f !important;
  box-shadow: 0 0 0 1px #054b1f !important;
}
.wp-core-ui.login input[type=checkbox] {
  border-radius: unset;
  border-color: #054b1f !important;
  color: #363636;
  background: transparent;
  font-size: 14px;
  transition: 0.3s ease all;
}
.wp-core-ui.login input[type=checkbox]:focus {
  border-color: #054b1f !important;
  box-shadow: 0 0 0 1px #054b1f !important;
}
.wp-core-ui.login input[type=checkbox]:checked {
  background: #054b1f !important;
  border-color: #054b1f !important;
  color: #fff;
}
.wp-core-ui.login input[type=checkbox]:hover {
  border-color: #054b1f !important;
}
.wp-core-ui.login input[type=submit] {
  line-height: 1 !important;
  min-height: unset;
  border-color: #054b1f !important;
}
.wp-core-ui.login input[type=submit]#wp-submit {
  width: 100% !important;
  float: left;
  margin-top: 20px;
}
.wp-core-ui.login .button.wp-hide-pw {
  background: transparent !important;
  color: #054b1f !important;
  height: auto;
  min-height: unset;
  min-width: unset;
}
.wp-core-ui.login .button.wp-hide-pw:hover {
  color: #363636 !important;
}
.wp-core-ui.login input.focus, .wp-core-ui.login input:focus,
.wp-core-ui.login .button.focus,
.wp-core-ui.login .button:focus {
  border-radius: 6px !important;
  border-color: #054b1f !important;
}
.wp-core-ui.login #language-switcher {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.wp-core-ui.login #language-switcher select {
  border-radius: 6px !important;
  border-color: #054b1f;
  height: 100% !important;
  margin: 0;
}

/* FONTS */
@font-face {
  font-family: "slick";
  src: url("https://example.de/wp-content/themes/dbw-base/assets/fonts/slick.woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
/* Config */
:root {
  --slick-arrow-color: #000;
  --dots-color: #000;
  --dots-scale: 1;
  --dots-gap: 5%;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: "";
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  height: 100%;
  min-height: 1px;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.slick-dots {
  position: absolute;
  left: 50%;
  margin: 0;
  transform: translateX(-50%);
  display: flex;
  gap: var(--dots-gap);
}

.slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
  scale: var(--dots-scale);
}

.slick-dots li button {
  list-style: none !important;
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  opacity: 0.2;
  background: transparent;
}

.slick-dots li button:hover {
  opacity: 0.5;
}

.slick-dots li button:before {
  content: "â€¢";
  font-size: 40px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  text-align: center;
  color: var(--dots-color); /* Config ist ganz oben Sichtbar */
}

.slick-dots li.slick-active button {
  opacity: 0.5;
}

.slick-prev.slick-arrow {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  left: -25px;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: transparent;
  opacity: 0.8;
}

.slick-prev.slick-arrow::before {
  content: "â†";
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  opacity: 0.75;
  color: var(--slick-arrow-color); /* Config ist ganz oben Sichtbar */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev.slick-arrow:hover {
  opacity: 1;
}

.slick-next.slick-arrow {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  right: -25px;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: transparent;
  opacity: 0.8;
}

.slick-next.slick-arrow::before {
  content: "â†’";
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  opacity: 0.75;
  color: var(--slick-arrow-color); /* Config ist ganz oben Sichtbar */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-next.slick-arrow:hover {
  opacity: 1;
}

