body {
  font-size: 16px;
  color: #363636;
  font-family: "Inter", Helvetica, Arial, sans-serif;
  line-height: 1.6;
  letter-spacing: -0.01em;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-variant-ligatures: common-ligatures;
  font-feature-settings: "kern" 1, "liga" 1, "calt" 1;
}

body,
button,
input,
select,
textarea {
  font-family: "Inter", Helvetica, Arial, sans-serif;
}

p {
  margin-bottom: 12.8px;
  font-size: 16px;
  line-height: 1.6;
  letter-spacing: -0.01em;
  color: #363636;
  text-wrap: pretty;
}
p:last-child {
  margin-bottom: 0;
}
p.text-small {
  font-size: 14.224px;
  line-height: 1.25;
}
p.text-large {
  font-size: 17.776px;
  line-height: 1.75;
}

a {
  color: inherit;
  text-decoration: underline;
  text-decoration-thickness: 1px;
  text-underline-offset: 0.15em;
  text-decoration-color: rgba(54, 54, 54, 0.4);
  transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  font-size: 16px;
}
a:hover {
  color: red;
  text-decoration-color: red;
  text-decoration-thickness: 2px;
  text-underline-offset: 0.2em;
  opacity: 0.9;
}
a:focus-visible {
  outline: 2px solid #054b1f;
  outline-offset: 2px;
  border-radius: 3px;
  text-decoration: none;
}

a,
a:hover,
a svg path,
button,
button:hover,
input[type=button] {
  transition: 0.3s ease all;
  text-decoration: none;
  cursor: pointer;
}

h1 {
  font-size: clamp(46.224px, 8vmin, 78.5808px);
  font-weight: 900;
  letter-spacing: -0.025em;
  line-height: 1.1;
  color: #222222;
  margin-bottom: 32px;
  text-align: left;
  font-feature-settings: "kern" 1, "liga" 1, "calt" 1, "ss01" 1;
  text-wrap: balance;
}

h2,
.h2 {
  font-size: clamp(35.552px, 5vmin, 42.6624px);
  color: #222222;
  font-weight: 800;
  letter-spacing: -0.025em;
  line-height: 1.25;
  margin-bottom: 25.6px;
  font-feature-settings: "kern" 1, "liga" 1, "calt" 1;
  text-wrap: balance;
}
h2--highlighted mark.gb-highlight,
.h2--highlighted mark.gb-highlight {
  position: relative;
  line-height: 1.3;
  background: transparent;
  color: inherit;
  padding: 0;
}
h2--highlighted mark.gb-highlight::after,
.h2--highlighted mark.gb-highlight::after {
  content: "";
  position: absolute;
  bottom: 0.1em;
  left: -0.05em;
  right: -0.05em;
  height: 0.15em;
  border-radius: 0.075em;
  z-index: -1;
  opacity: 0.8;
}
h2--highlighted__primary mark.gb-highlight::after,
.h2--highlighted__primary mark.gb-highlight::after {
  background: linear-gradient(90deg, #054b1f, rgb(9.78125, 146.71875, 60.64375));
}
h2--highlighted__white mark.gb-highlight::after,
.h2--highlighted__white mark.gb-highlight::after {
  background: #fff;
}
h2--highlighted__body mark.gb-highlight::after,
.h2--highlighted__body mark.gb-highlight::after {
  background: #363636;
}
h2--highlighted__centered,
.h2--highlighted__centered {
  text-align: center;
  margin-bottom: 19.2px;
}
h2--highlighted__centered mark.gb-highlight::after,
.h2--highlighted__centered mark.gb-highlight::after {
  left: 50%;
  transform: translateX(-50%);
}

h3 {
  font-size: clamp(28.448px, 5vmin, 31.2928px);
  line-height: 1.25;
  font-weight: 700;
  color: #222222;
  letter-spacing: -0.025em;
  margin-bottom: 25.6px;
  text-wrap: balance;
}

h4 {
  font-size: clamp(24.896px, 5vmin, 24.896px);
  line-height: 1.25;
  font-weight: 700;
  color: #222222;
  letter-spacing: -0.01em;
  margin-bottom: 19.2px;
}

h5 {
  font-size: 21.328px;
  line-height: 1.25;
  font-weight: 600;
  color: #222222;
  letter-spacing: -0.01em;
  margin-bottom: 19.2px;
}

h6 {
  font-size: 17.776px;
  line-height: 1.25;
  font-weight: 600;
  color: #222222;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 19.2px;
}

.text-highlight {
  color: #054b1f;
  font-weight: 600;
}

.text-muted {
  color: #6a747c;
  font-weight: 400;
}

.text-small {
  font-size: 14.224px;
  line-height: 1.25;
}

.text-large {
  font-size: 17.776px;
  line-height: 1.75;
  font-weight: 400;
}

.text-center {
  text-align: center;
}

.text-balance {
  text-wrap: balance;
}

.text-pretty {
  text-wrap: pretty;
}

mark,
.gb-highlight {
  color: inherit;
  background: transparent;
}

.highlight-text {
  color: #054b1f;
  font-weight: 600;
}

@media (max-width: 768px) {
  body {
    font-size: 15.04px;
  }
  h1 {
    margin-bottom: 25.6px;
  }
  h2,
  .h2 {
    margin-bottom: 20.48px;
  }
}
@media (max-width: 576px) {
  body {
    font-size: 14.24px;
    line-height: 1.75;
  }
  p {
    margin-bottom: 15.36px;
  }
  a {
    text-underline-offset: 0.2em;
  }
}
@media print {
  body {
    font-size: 12pt;
    line-height: 1.4;
    color: black;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: black;
    page-break-after: avoid;
  }
  p {
    orphans: 3;
    widows: 3;
  }
}
.valign--center {
  align-items: center;
}

.offset--top__sm {
  margin-top: 50px;
}
.offset--top__md {
  margin-top: 100px;
}

.text--narrow {
  width: 100%;
  max-width: 66%;
}
@media (max-width: 1200px) {
  .text--narrow {
    max-width: 80%;
  }
}
@media (max-width: 768px) {
  .text--narrow {
    max-width: 100%;
  }
}

.btn-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.stretch-link {
  position: relative;
}
.stretch-link a::after {
  content: "";
  position: absolute;
  inset: 0;
}
.stretch-link a:is(:focus-visible)::after {
  outline: 2px solid;
}
.stretch-link a:is(:hover, :focus) {
  outline: none;
}

#page {
  position: relative;
  overflow: unset;
  display: block;
  background: #fff;
}

#content .site-main {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}
#content .site-main .inside-article {
  padding: 0;
}
#content .site-main .inside-article .entry-content > *:not(.section--fullwidth,
.section--fullwidth-compact,
.section--fullwidth-compact-bottom,
.section--fullwidth-compact-top) {
  width: 80%;
  max-width: 1200px;
  margin-inline: auto;
}
#content .site-main article {
  margin-bottom: 0;
}

.container {
  position: relative;
  max-width: 1600px;
  width: 100%;
  margin: 0 auto;
  display: block;
}

.site {
  max-width: unset;
  padding: 0;
}
.site .inside-article {
  padding: 0;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background: transparent;
}

body.stop {
  overflow: hidden;
}

div {
  box-sizing: border-box;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
