  --cf7-font-size: 16px;
  --cf7-primary: #054b1f;
  --cf7-primary-hover: #076d2a;
  --cf7-text: #363636;
  --cf7-text-light: #6a747c;
  --cf7-border: #f2f2f2;
  --cf7-border-focus: #054b1f;
  --cf7-bg: #fff;
  --cf7-bg-light: #f8f8f8;
  --cf7-success: #10b981;
  --cf7-error: red;
  --cf7-radius: 6px;
  --cf7-shadow: 0 2px 8px rgba(34, 34, 34, 0.1);
  --cf7-shadow-focus: 0 0 0 3px rgba(5, 75, 31, 0.15);
