}

/* ----------------------------------
   Submit Button
---------------------------------- */
.form-group input[type=submit] {
  background: linear-gradient(135deg, #054b1f 0%, rgb(7.55, 113.25, 46.81) 50%, rgb(8.825, 132.375, 54.715) 100%);
  color: white;
  border: none;
  padding: 14px 28px;
  border-radius: var(--cf7-radius);
  font-family: var(--cf7-font);
  font-size: 16px;
  font-weight: 600;
  letter-spacing: -0.02em;
  cursor: pointer;
  transition: 0.3s ease all;
  box-shadow: var(--cf7-shadow);
  min-height: 48px;
  margin-top: 25px;
}
.form-group input[type=submit]:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(34, 34, 34, 0.15);
  filter: brightness(1.05);
}
.form-group input[type=submit]:focus {
  outline: none;
  box-shadow: var(--cf7-shadow-focus), 0 4px 16px rgba(34, 34, 34, 0.15);
}
.form-group input[type=submit]:active {
  transform: translateY(0);
}
.form-group input[type=submit]:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}
.form-group input[type=submit]:disabled:hover {
  transform: none;
  filter: none;
}

/* ----------------------------------
   Error States & Validation
---------------------------------- */
.wpcf7-not-valid-tip {
  display: block;
  color: var(--cf7-error);
  font-size: 12px;
  margin-top: 6px;
  font-weight: 500;
  order: 2;
}

.wpcf7-form.init .wpcf7-not-valid,
.wpcf7-form:not(.invalid):not(.sent):not(.failed) .wpcf7-not-valid {
  border-color: var(--cf7-border) !important;
  box-shadow: none !important;
}
.wpcf7-form.init .wpcf7-not-valid-tip,
.wpcf7-form:not(.invalid):not(.sent):not(.failed) .wpcf7-not-valid-tip {
  display: none !important;
}

.wpcf7-form.invalid .wpcf7-not-valid {
  border-color: var(--cf7-error) !important;
  box-shadow: 0 0 0 3px rgba(255, 0, 0, 0.1) !important;
}
.wpcf7-form.invalid .wpcf7-not-valid-tip {
  display: block !important;
}

/* ----------------------------------
   Response Messages - Modern Status Design (Parent-scoped)
---------------------------------- */
.wpcf7-response-output {
  margin: 50px 0 !important;
  padding: 25px !important;
  border-radius: var(--cf7-radius) !important;
  border: none !important;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 500;
  position: relative;
  overflow: hidden;
  display: block !important;
  padding-left: calc(25px + 24px + 12px) !important;
}
.wpcf7-response-output::before {
  position: absolute;
  left: 25px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
  font-weight: bold;
  background: white;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.wpcf7-response-output:empty {
  display: none !important;
}

/* âœ… Erfolgreich gesendet */
form.sent .wpcf7-response-output {
  background: linear-gradient(135deg, rgb(205.2985074627, 250.7014925373, 235.6567164179) 0%, rgb(181.828358209, 248.671641791, 226.5223880597) 100%);
  color: rgb(11.9402985075, 138.0597014925, 96.2686567164);
  border: 2px dashed rgb(64.4776119403, 238.5223880597, 180.8507462687) !important;
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.15), inset 0 1px 0 rgba(255, 255, 255, 0.8);
  animation: successPulse 0.6s ease-out;
}
form.sent .wpcf7-response-output::before {
  content: "âœ“";
  color: #10b981;
  box-shadow: 0 2px 4px rgba(16, 185, 129, 0.2);
}

/* âŒ Fehler beim Absenden */
form.invalid .wpcf7-response-output {
  background: linear-gradient(135deg, white 0%, rgb(255, 229.5, 229.5) 100%);
  color: #cc0000;
  border: 2px dashed #ff6666 !important;
  box-shadow: 0 4px 12px rgba(255, 0, 0, 0.15), inset 0 1px 0 rgba(255, 255, 255, 0.8);
  animation: errorShake 0.6s ease-out;
}
form.invalid .wpcf7-response-output::before {
  content: "âš ";
  color: red;
  box-shadow: 0 2px 4px rgba(255, 0, 0, 0.2);
}

/* ----------------------------------
   Animations
---------------------------------- */
@keyframes successPulse {
  0% {
    transform: scale(0.95);
    opacity: 0;
  }
  50% {
    transform: scale(1.02);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes errorShake {
  0%, 100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-4px);
  }
  75% {
    transform: translateX(4px);
  }
}
/* ----------------------------------
   Responsive Design
---------------------------------- */
@media (max-width: 768px) {
  .wpcf7 input:not([type=submit]):not([type=checkbox]):not([type=radio]),
  .wpcf7 textarea,
  .wpcf7 select {
    font-size: 16px !important;
  }
