/* ----------------------------------------------------------
   Basic Card Styles
---------------------------------------------------------- */
.card {
  background: #fff;
  border-radius: 6px;
  box-shadow: 0 2px 8px rgba(34, 34, 34, 0.1);
  padding: 50px;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  /* ----------------------------------
     Card Variations
  ---------------------------------- */
  /* ----------------------------------
     Responsive Behavior
  ---------------------------------- */
  /* ----------------------------------
     Accessibility
  ---------------------------------- */
}
.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 16px rgba(34, 34, 34, 0.15);
}
.card__header {
  margin-bottom: 25px;
}
.card__header .card__title {
  font-size: 24.896px;
  font-weight: 700;
  color: #222222;
  margin-bottom: 12.5px;
  line-height: 1.1;
  margin-bottom: 0;
}
.card__header .card__subtitle {
  font-size: 14.224px;
  color: #6a747c;
  font-weight: 400;
}
.card__body {
  margin-bottom: 25px;
}
.card__body p {
  font-size: 16px;
  color: #363636;
  line-height: 1.6;
  margin-bottom: 12.5px;
}
.card__body p:last-child {
  margin-bottom: 0;
}
.card__footer {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 25px;
}
.card__image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 6px;
  margin-bottom: 25px;
}
.card--compact {
  padding: 25px;
  border-radius: 3px;
}
.card--compact .card__header .card__title {
  font-size: 21.328px;
}
.card--compact .card__image {
  height: 150px;
  border-radius: 3px;
}
.card--large {
  padding: 100px;
  border-radius: 9px;
}
.card--large .card__header .card__title {
  font-size: 28.448px;
}
.card--large .card__image {
  height: 250px;
  border-radius: 9px;
}
.card--outlined {
  background: transparent;
  border: 2px solid #f2f2f2;
  box-shadow: none;
}
.card--outlined:hover {
  border-color: #054b1f;
  box-shadow: 0 2px 8px rgba(34, 34, 34, 0.1);
}
.card--glass {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(16px);
  box-shadow: 0 2px 8px rgba(34, 34, 34, 0.1);
}
.card--glass:hover {
  background: rgba(255, 255, 255, 0.9);
  border-color: rgba(255, 255, 255, 0.3);
}
.card--primary {
  background: linear-gradient(135deg, #054b1f 0%, rgb(7.55, 113.25, 46.81) 50%, rgb(8.825, 132.375, 54.715) 100%);
  color: #fff;
}
.card--primary .card__title,
.card--primary .card__subtitle,
.card--primary .card__body p {
  color: #fff;
}
.card--primary .card__footer {
  border-color: rgba(255, 255, 255, 0.2);
}
.card--primary:hover {
  filter: brightness(1.1);
}
@media (max-width: 768px) {
  .card {
    padding: 25px;
  }
  .card .card__image {
    height: 180px;
  }
  .card--large {
    padding: 50px;
  }
  .card--large .card__image {
    height: 200px;
  }
}
.card:focus-within {
  box-shadow: 0 0 0 3px rgba(5, 75, 31, 0.2), 0 4px 16px rgba(34, 34, 34, 0.15);
  outline: none;
}
@media (prefers-reduced-motion: reduce) {
  .card {
    transition: none;
  }
  .card:hover {
    transform: none;
  }
}

