---------------------------------------------------------- */
.card.contact {
  /* Mobile Responsive */
}
.card.contact .card__image {
  width: 100%;
  aspect-ratio: 1/1;
  height: auto;
  object-fit: cover;
  border-radius: 6px;
  margin-bottom: 25px;
}
.card.contact.card--compact .card__image {
  aspect-ratio: 1/1;
  height: auto;
  border-radius: 3px;
}
.card.contact.card--large .card__image {
  aspect-ratio: 1/1;
  height: auto;
  border-radius: 9px;
}
@media (max-width: 768px) {
  .card.contact .card__image {
    aspect-ratio: 1/1;
    height: auto;
  }
  .card.contact.card--large .card__image {
    aspect-ratio: 1/1;
    height: auto;
  }
}

.dbw-parallax {
  position: relative;
  min-height: 60vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-attachment: fixed !important;
  background-image: var(--inline-bg-image) !important;
  background-repeat: no-repeat !important;
  background-size: cover !important;
}
